import Vue from 'vue'
import Router from 'vue-router'
const _import = file => () => import('@/' + file + '.vue');

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'home',
      component: _import("view/home"),
      redirect: '/home/TransferPayment',
      children: [{
        path: '/home/TransferPayment',
        name: 'TransferPayment',
        component: _import("view/TransferPayment/index")
      }, {
        path: '/home/MyAccount',
        name: 'MyAccount',
        component: _import("view/MyAccount/index")
      }, {
        path: '/home/PersonalCenter',
        name: 'PersonalCenter',
        component: _import("view/PersonalCenter/index")
      }]
    },
    {
      path: '/contact',
      name: 'contact',
      component: _import("components/contact")
    },
    {
      path: '/language',
      name: 'language',
      component: _import("view/PersonalCenter/languageChange")
    },
    {
      path: '/comingSoon',
      name: 'comingSoon',
      component: _import("view/comingSoon")
    },
    {
      path: '/walletManage',
      name: 'walletManage',
      component: _import("view/PersonalCenter/walletManage/index")
    },
    {
      path: '/importWallet',
      name: 'importWallet',
      component: _import("view/PersonalCenter/walletManage/importWallet")
    }
  ]
})


