import { isEmptyObject } from '@/js/utils';
import { jcWalletApi } from "@/lib";
import Lockr from "lockr";
Lockr.prefix = 'jingchang_';
let wallet = jcWalletApi.getJCWallet() || null
const jcWallet = {
  state: {
    fullInfo: wallet
  },
  mutations: {
    SET_JCWALLET(state, wallet) {
      if (isEmptyObject(wallet)) {
        state.fullInfo = null
      } else {
        state.fullInfo = wallet
      }
    }
  },
  actions: {
    updateJCWallet({
      commit,
      state
    }, wallet) {
      commit('SET_JCWALLET', wallet);
    }
  },
  getters: {
    swtAddress(state) {
      let address = jcWalletApi.getAddress(state.fullInfo, 'swt');
      return address || '';
    },
    ethAddress(state) {
      let address = jcWalletApi.getAddress(state.fullInfo, 'eth');
      return address || '';
    },
    stmAddress(state) {
      let address = jcWalletApi.getAddress(state.fullInfo, 'stm');
      return address || '';
    },
    callAddress(state) {
      let address = jcWalletApi.getAddress(state.fullInfo, 'call');
      return address || '';
    },
    moacAddress(state) {
      let address = jcWalletApi.getAddress(state.fullInfo, 'moac');
      return address || '';
    },
    bizAddress(state) {
      let address = jcWalletApi.getAddress(state.fullInfo, 'biz');
      return address || '';
    },
    jcWallet: state => state.fullInfo
  }
};

export default jcWallet;


