import { Vue } from 'lib';
const jcCoins = {
  state: {
    coins: null
  },
  mutations: {
    SET_COINS(state, coins) {
      Vue.set(state, 'coins', coins)
    }
  },
  actions: {
    updateCoins: ({
      commit
    }, coins) => commit('SET_COINS', coins)
  },
  getters: {
    coins: state => state.coins
  }
}
export default jcCoins;


